@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf");
  font-style: normal;
  font-weight: normal;
}

@font-face {
 font-family: "Roboto-Bold";
 src: url("../fonts/Roboto-Bold.ttf");
 font-style: normal;
 font-weight: 800;
}

@font-face {
 font-family: "FiraSans";
 src: url("../fonts/FiraSans-Regular.ttf");
 font-style: normal;
 font-weight: normal;
}

@font-face {
font-family: "FiraSans-Bold";
src: url("../fonts/FiraSans-Bold.ttf");
font-style: normal;
font-weight: 800;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
}

/* remember to define focus styles! */
:focus {
  outline: 0;
}

ol,
ul {
  list-style: none;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: separate;
  border-spacing: 0;
}

caption,
th,
td {
  text-align: left;
  font-weight: normal;
}

blockquote,
q {
  quotes: "" "";
}

*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img, svg {
  width: 100%;
  height: auto;
}

.flex {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.flex.space-between {
  -ms-flex-pack: justify;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.flex.around {
  -ms-flex-pack: distribute;
  justify-content: space-around;
}

.flex.end {
  -ms-flex-pack: end;
  -webkit-box-pack: end;
  justify-content: flex-end;
}

.flex.start {
  -ms-flex-pack: start;
  -webkit-box-pack: start;
  justify-content: flex-start;
}

.flex.wrap {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.flex.top {
  -ms-flex-align: start;
  -webkit-box-align: start;
  align-items: flex-start;
}

.flex.center {
  -ms-flex-pack: center;
  -webkit-box-pack: center;
  justify-content: center;
}

.flex.middle {
  -ms-flex-align: center;
  -webkit-box-align: center;
  align-items: center;
}

.flex.bottom {
  -ms-flex-align: end;
  -webkit-box-align: end;
  align-items: flex-end;
}

.flex.column {
  -ms-flex-direction: column;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
}

body::-webkit-scrollbar {
  width: 0;
}

body {
  scrollbar-width: none;
  -ms-overflow-style: none;
  width: 100%;
  position: relative;
}

.img-wrap {
  line-height: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
}

.img-wrap img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

$selectric-main-color:      #0b73a3 !default;                                              // Color used in border
$selectric-secondary-color: #0b73a3 !default;                                              // Color used in button
$selectric-text-color:      #0b73a3 !default;                                              // Color used in label
$selectric-bg-color:        #F8F8F8 !default;                                           // Background color
$selectric-btn-bg-color:    #F8f8f8 !default;                                           // Button background color
$selectric-height:          40px !default;                                              // Outer height
$selectric-spacing:         10px !default;                                              // Label left padding
$selectric-border-width:    1px !default;                                               // Outer border width
$selectric-border-radius:   0px !default;                                               // Border radius
$selectric-inner-height:    $selectric-height - ($selectric-border-width * 2) !default; // Inner height
$selectric-font-size:       12px !default;                                              // Font size

.selectric-wrapper {
  position: relative;
  cursor: pointer;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border: $selectric-border-width solid $selectric-main-color;
  border-radius: $selectric-border-radius;
  background: $selectric-bg-color;
  position: relative;
  overflow: hidden;
  border-radius: 5px;

  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 $selectric-inner-height 0 $selectric-spacing;
    font-size: $selectric-font-size;
    line-height: $selectric-inner-height;
    color: $selectric-text-color;
    height: $selectric-inner-height;
    user-select: none;
    font-family: 'Roboto-Bold';
  }

  .button {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: $selectric-inner-height;
    height: $selectric-inner-height;
    line-height: $selectric-inner-height;
    background-color: $selectric-btn-bg-color !important;
    color: $selectric-secondary-color;
    text-align: center;
    font: 0/0 a;
    *font: 20px/#{$selectric-inner-height} Lucida Sans Unicode, Arial Unicode MS, Arial;

    &:after {
      content: " ";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 0;
      height: 0;
      border: 4px solid transparent;
      border-top-color: $selectric-secondary-color;
      border-bottom: none;
    }
  }
}

.selectric-focus .selectric {
  border-color: darken($selectric-main-color, 0%);
}

.selectric-hover .selectric {
  border-color: darken($selectric-main-color, 0%);

  .button {
    color: darken($selectric-secondary-color, 0%);

    &:after {
      border-top-color: darken($selectric-secondary-color, 0%);
    }
  }
}

.selectric-open {
  z-index: 89;

  .selectric {
    border-color: darken($selectric-main-color, 10%);
  }

  .selectric-items {
    display: block;
  }
}

.selectric-disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default;
  user-select: none;
}

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;

  select {
    position: absolute;
    left: -100%;
  }

  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;

    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0;
    }
  }
}

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important;
}

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important;
}

/* Items box */
.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: $selectric-bg-color;
  border: 1px solid darken($selectric-main-color, 0%);
  z-index: -1;
  box-shadow: 0 0 10px -6px;

  .selectric-scroll {
    height: 100%;
    overflow: auto;
  }

  .selectric-above & {
    top: auto;
    bottom: 100%;
  }

  ul, li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: $selectric-font-size;
    line-height: 20px;
    min-height: 20px;
  }

  li {
    display: block;
    padding: 10px;
    color: #666;
    cursor: pointer;

    &.selected {
      background: #0b73a3;
      color: white;
    }

    &.highlighted {
      background: #0b73a3;
      color: white;
    }

    &:hover {
      background: #0b73a3;
      color: white;
    }
  }

  .disabled {
    filter: alpha(opacity=50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    user-select: none;
  }

  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      user-select: none;
      background: none;
      color: #444;
    }

    &.disabled li {
      filter: alpha(opacity=100);
      opacity: 1;
    }

    li {
      padding-left: 25px;
    }
  }
}

$blue: #0b73a3;
$red: #b33939;
$green: #28a745;
$grey: #424242;
$grey2: #212121;

body {
    font-family: "Roboto";
    line-height: 1.2;
}

.container {
    width: 80%;
    margin: 0 auto;
}

.container2 {
    width: 94%;
    margin: 0 auto;
}

section {
    position: relative;
}

.button {
    background: $green;
    border-radius: 4px;
    height: 62px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    transition: all 0.35s ease-in;

    &:hover {
        background: #24853b;
    }

    span {
        position: relative;
        z-index: 10;
        color: white;
        font-size: 18px;
        text-transform: uppercase;
        display: block;
    }

    svg {
        width: 15px;
        margin-left: 10px;
    }
}

h1 {
    font-family: "Roboto-Bold";
    color: $red;
    font-size: 48px;
    margin-bottom: 36px;
}

h3 {
    font-family: "FiraSans";
    font-size: 30px;
    line-height: 1.6;
    color: $grey2;
}

p {
    font-family: "FiraSans";
    color: $grey;
    font-size: 20px;
}

header {
    width: 100%;
    height: 140px;
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    padding: 30px 0;
    font-family: "Roboto-Bold";

    &::before {
        content: '';
        width: 100%;
        height: 0;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        transition: all 0.45s cubic-bezier(0.075, 0.82, 0.165, 1);
        background: white;
    }

    &.active::before {
        height: 100%;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    }

    svg.mob {
        display: none;
    }

    .col4 {
        display: none;
    }

    .header-menu-wrap {
        ul {
            li {
                margin-right: 40px;
                a {
                    font-size: 30px;
                    line-height: 1.2;
                    color: $blue;
                    transition: all 0.35s ease-in;
                    position: relative;

                    &::after {
                        content: '';
                        position: absolute;
                        bottom: -5px;
                        left: 0;
                        width: 100%;
                        height: 0px;
                        background: $blue;
                        transition: all 0.2s ease-in;
                    }

                    &:hover::after {
                        height: 3px;
                    }
                }
            }
        }
    }

    .phone-wrap {
        a {
            font-size: 36px;
            color: $red;
        }
    }
}

.logo-wrap {
    display: block;
    width: 160px;
}

.menu {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: white;
    z-index: 900;
    padding: 120px 3% 0 3%;
    transform: translate(-100%, 0);
    transition: all 0.45s cubic-bezier(0.075, 0.82, 0.165, 1);
    display: none;
    max-width: 400px;

    &.active {
        transform: translate(0, 0);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    }

    ul {
        li {
            margin-bottom: 15px;
            a {
                color: $blue;
                font-size: 32px;
                font-family: 'Roboto-Bold';
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    bottom: -4px;
                    left: 0;
                    width: 100%;
                    height: 0px;
                    background: $blue;
                    transition: all 0.2s ease-in;
                }

                &:hover::after {
                    height: 3px;
                }
            }
        }
    }
}

.burger-wrap {
    position: absolute;
    z-index: 100;
    top: 28px;
    right: 3%;

    .icon {
        width: 40px;
        height: 40px;
        cursor: pointer;
    }
    .burger {
        width: 32px;
        height: 4px;
        background-color: $blue;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
        transition: 0.5s;
    }
    .burger::before,
    .burger::after {
        content: "";
        position: absolute;
        width: 32px;
        height: 4px;
        background: $blue;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
        transition: 0.5s;
    }
    .burger::before {
        top: -8px;
    }
    .burger::after {
        top: 8px;
    }

    .icon.active .burger {
        background: rgba(0, 0, 0, 0);
        box-shadow: none;
    }
    .icon.active .burger::before {
        top: 0;
        transform: rotate(45deg);
    }
    .icon.active .burger::after {
        top: 0;
        transform: rotate(135deg);
    }
}

.language-wrap {
    display: flex;
    align-items: center;
    span {
        display: block;
        margin: 0 12px;
        color: black !important;
    }
    a, span {
        color: $red;
        font-size: 24px;
        font-family: 'Roboto-Bold';
    }

    a {
        transition: all 0.25s ease-in;

        &:hover {
            opacity: 0.6;
        }
    }
}

// Sect 1
.mp_s1 {
    height: 100vh;
    overflow: hidden;
    background: radial-gradient(
        62.25% 125.42% at 50% 49.36%,
        #f5f5f5 0%,
        #eeeeee 19.72%,
        #e0e0e0 37.26%,
        #bdbdbd 53.98%,
        #9e9e9e 70.19%,
        #757575 85.89%,
        #616161 100%
    );
    border-radius: 0px 0px 40px 0px;

    .container {
        height: 100%;
        position: relative;
        z-index: 1;
    }

    .bg {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        z-index: 0;

        .desc {
            width: 60vw;
        }
        .tab {
            display: none;
        }
    }

    h1, h3 {
        max-width: 600px;
    }
}

// Sect 2
.mp_s2 {
    margin: 70px 0;
    padding: 50px 0;
    background: #eeeeee;

    h2 {
        color: $blue;
        font-size: 36px;
        line-height: 1.6;
        font-family: "FiraSans";
        margin-bottom: 20px;
    }
}

// Sect 3
.mp_s3 {
    .col1 {
        width: 40%;

        .slider-wrap {
            position: relative;
            height: 350px;

            .button {
                position: absolute;
                bottom: 30px;
                right: 30px;
                z-index: 60;
                background: $blue;
                padding: 0 15px;
                height: 36px;

                &:hover {
                    background: #095b81;
                }

                span {
                    font-size: 14px;
                }

                svg {
                    width: 11px;
                }
            }

            .abs {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
            }

            .block {
                width: 100%;
                height: 100%;
                background: #fafafa;
                border: 1px solid #eeeeee;
                box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12),
                    0px 1px 5px rgba(0, 0, 0, 0.2);
                border-radius: 20px;
                position: absolute;
                top: 0;
                left: 0;

                &.block1 {
                    z-index: 30;
                }
                &.block2 {
                    z-index: 20;
                    transform: translate(9px, 8px);
                }
                &.block3 {
                    z-index: 10;
                    transform: translate(18px, 16px);
                }
            }

            .abs2 {
                z-index: 50;

                .item {
                    padding: 30px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    opacity: 0;
                    pointer-events: none;
                    transition: all 0.35s ease-in;
                    transform: translateX(-20px);

                    &.active {
                        opacity: 1;
                        pointer-events: all;
                        transition: all 0.35s ease-in 0.45s;
                        transform: translateX(0);
                    }

                    &.active2 {
                        opacity: 0;
                        transition: all 0.35s ease-in 0s;
                        transform: translateX(20px);
                    }

                    h2 {
                        color: $blue;
                        font-size: 24px;
                        margin-bottom: 20px;
                    }

                    ul {
                        padding-left: 20px;

                        li {
                            padding-left: 10px;
                            font-size: 18px;
                            color: $grey;
                            margin-bottom: 20px;
                            position: relative;

                            &::before {
                                content: "";
                                width: 4px;
                                height: 4px;
                                background: $grey;
                                border-radius: 50%;
                                position: absolute;
                                top: 5px;
                                left: 0;
                            }
                        }
                    }
                }
            }
        }
    }
    .col2 {
        width: 50%;

        ul {
            margin-top: 35px;
            li {
                display: flex;
                margin-bottom: 25px;
                svg {
                    width: 32px;
                    height: auto;
                }
                span {
                    display: block;
                    margin-right: 20px;
                    transform: translateY(5px);
                }
                p {
                    font-size: 30px;
                }
            }
        }
    }
}

// Sect 4
.mp_s4 {
    margin: 70px 0;
    .container {
        width: 90%;
        margin: 0;
        margin-left: 10%;
    }

    .flex-wrap {
        margin-top: 35px;
    }

    .col1 {
        width: 50%;

        p {
            margin-bottom: 35px;
        }

        .video-wrap {
            width: 100%;
            position: relative;

            .video {
                width: 100%;
                border-radius: 10px;
                overflow: hidden;
                box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.14), 0px 1px 18px rgba(0, 0, 0, 0.12),
                    0px 3px 5px rgba(0, 0, 0, 0.2);
                position: absolute;
                top: 0;
                left: 0;
                transition: all 0.35s ease-in;
                opacity: 0;
                pointer-events: none;
                line-height: 0;

                &.active {
                    opacity: 1;
                    pointer-events: all;
                    translate: all 0.35s ease-in 0.4s;
                }

                iframe {
                    width: 100%;
                }
            }
        }
    }

    .col2 {
        width: 48%;

        .tab-wrap {
            background: #f5f5f5;
            box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.14), 0px 1px 18px rgba(0, 0, 0, 0.12),
                0px 3px 5px rgba(0, 0, 0, 0.2);
            border-radius: 40px 0px 0px 40px;
            padding: 80px;

            .content {
                padding: 20px 0;
                p {
                    display: none;
                    margin-top: 20px;
                }
            }

            .tab {
                .line {
                    width: 100%;
                    height: 1px;
                    background: #e0e0e0;
                    position: relative;

                    span {
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 2px;
                        width: 0;
                        background: $blue;
                        transition: all 0.25s ease-in;
                    }
                }

                &.active {
                    .line {
                        span {
                            width: 50%;
                        }
                    }
                }

                .title-wrap {
                    cursor: pointer;
                    .svg-wrap {
                        margin-right: 30px;
                        padding-top: 13px;
                        svg {
                            width: 44px;
                        }
                    }
                    h1 {
                        font-size: 36px;
                        margin-bottom: 5px;
                    }
                    span {
                        font-size: 18px;
                        font-family: "FiraSans-Bold";
                        color: $grey;
                    }
                }
            }
        }
    }
}

// Sect 6
.mp_s6 {
    .block {
        background: #e9e9e9;
        border: 1px solid #e9e9e9;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 1px 5px rgba(0, 0, 0, 0.2);
        border-radius: 10px;
        margin-bottom: 40px;
        padding: 20px;
        display: flex;
        justify-content: space-between;

        &.block1 {
            img {
                width: 26vw;
            }
        }

        &.block2 {
            img {
                width: 36vw;
            }
        }

        &.block3 {
            .img-wrap {
                justify-content: flex-start;
            }
            img {
                width: 18vw;
            }
        }

        .col {
            width: 49%;
        }

        h3 {
            font-size: 36px;
            color: $blue;
            margin-bottom: 20px;
        }

        ul {
            li {
                color: $grey;
                font-size: 30px;
                margin-bottom: 20px;
                display: flex;

                span {
                    display: block;
                    &:first-child {
                        margin-right: 8px;
                    }
                }
            }
        }
    }
}

// Sect 7
.mp_s7 {
    margin-bottom: 70px;

    .select-wrap {
        margin-bottom: 30px;
    }
    .selectric-wrapper {
        width: 200px;
        margin-top: 15px;
    }

    .content-wrap {
        padding: 40px;
        background: #F5F5F5;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.14), 0px 1px 18px rgba(0, 0, 0, 0.12), 0px 3px 5px rgba(0, 0, 0, 0.2);
        border-radius: 10px;
    }

    .tarif-wrap {
        position: relative;
    }

    .flex-wrap {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transition: all 0.35s ease-in;
        transform: translateX(-20px);
        opacity: 0;
        pointer-events: none;

        &.active {
            opacity: 1;
            pointer-events: all;
            transition: all 0.35s ease-in 0.45s;
            transform: translateX(0);
        }

        &.active2 {
            opacity: 0;
            transition: all 0.35s ease-in 0s;
            transform: translateX(20px);
        }

        .block {
            width: 450px;
            border: 1px solid #BDBDBD;
            border-radius: 8px;
            background: #F5F5F5;
            box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.14), 0px 1px 18px rgba(0, 0, 0, 0.12), 0px 3px 5px rgba(0, 0, 0, 0.2);
            padding: 20px;
            padding-bottom: 80px;
            position: relative;
            margin: 0 15px;

            .green {
                color: $green;
            }
            .blue {
                color: $blue;
            }

            &.base.hidden {
                display: none;
            }

            h2 {
                font-size: 32px;
                margin-bottom: 12px;
            }
            p {
                color: #424242;
            }

            .inner {
                padding: 20px 0;
                margin: 20px 0;
                border-top: 1px solid #9E9E9E;
                border-bottom: 1px solid #9E9E9E;

                h4 {
                    margin-top: 5px;
                }
            }

            ul {
                li {
                    display: flex;
                    margin-bottom: 12px;
                    font-size: 14px;

                    svg {
                        width: 14px;
                        margin-right: 10px;
                    }
                }
            }

            .button-wrap {
                position: absolute;
                bottom: 20px;
                left: 50%;
                transform: translate(-50%, 0);

                .button {
                    height: 42px;
                }
            }
        }
    }
}

// Sect 8
.mp_s8 {
    .block {
        background: #fafafa;
        border: 1px solid #e0e0e0;
        box-shadow: inset -1px -1px 0px #eeeeee, inset 1px 1px 0px #eeeeee;
        border-radius: 8px;
        margin-bottom: 10px;
        .title {
            padding: 15px 20px 15px 30px;
            border-bottom: 1px solid #e0e0e0;
            h3 {
                margin: 0;
                color: $blue;
                max-width: calc(100% - 40px);
            }
            cursor: pointer;
        }
        .content {
            padding: 15px 40px;
            display: none;
        }
        .svg-wrap {
            width: 32px;
            padding: 10px;
            cursor: pointer;
            transition: all 0.3s ease-in;
        }
        &.active {
            .svg-wrap {
                transform: rotate(180deg);
            }
        }
    }
}

// Sect 9
.mp_s9 {
    margin: 70px 0;
    padding: 40px 0;
    position: relative;
    overflow: hidden;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.14), 0px 3px 1px rgba(0, 0, 0, 0.12), 0px 1px 5px rgba(0, 0, 0, 0.2);

    .form-back {
        background: #eeeeee;
        position: absolute;
        width: 100%;
        height: 105%;
        top: 0;
        left: 0;
        z-index: -1;
        overflow: hidden;
        line-height: 0;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    h1 {
        text-align: center;
        color: $blue;
        padding: 0 20px;

        span {
            color: $red;
        }
    }

    .col1 {
        width: 60%;
        margin-left: 5%;

        h2 {
            font-size: 40px;
            color: rgb(54, 54, 54);
            margin-bottom: 15px;

            span {
                color: black;
            }
        }
    }

    .col2 {
        width: 32%;

        .form-wrap {
            background: white;
            box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.14), 0px 1px 18px rgba(0, 0, 0, 0.12),
                0px 3px 5px rgba(0, 0, 0, 0.2);
            border-radius: 10px;
            padding: 20px;

            .button {
                margin-top: 50px;
                width: 160px;
                height: 42px;

                a {
                    span {
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

.input-wrap {
    margin-bottom: 30px;
    input {
        display: block;
        width: 100%;
        height: 56px;
        outline: none;
        background: #ffffff;
        border: 1px solid #bdbdbd;
        border-radius: 4px;
        padding-left: 15px;

        font-size: 16px;
        color: #616161;
    }
}

footer {
    background: radial-gradient(
        62.25% 125.42% at 50% 49.36%,
        #f5f5f5 0%,
        #eeeeee 19.72%,
        #e0e0e0 37.26%,
        #bdbdbd 53.98%,
        #9e9e9e 70.19%,
        #757575 85.89%,
        #616161 100%
    );
    padding: 70px 0;

    .col1 {
        width: 16%;
    }
    .col2 {
        width: 16%;
        .address {
            font-size: 24px;
        }
    }
    .col3 {
        .phone {
            color: $blue;
            font-size: 36px; 
            font-family: "Roboto-Bold";
        }
    }
    .col4 {
        .soc-wrap {
            a {
                margin: 0 15px;
                display: block;
                svg {
                    width: 50px;
                }
            }
        }
    }
}

// For anim 
header .col1,
header ul li, 
header .col3 .phone-wrap, 
header .language-wrap, 
header .col4 .burger-wrap,
.mp_s1 h1, 
.mp_s1 h3 {
    transform: translateX(-20px);
    opacity: 0;
    transition: all 0.5s ease-out;
}

.mp_s1 .bg {
    opacity: 0;
    transition: all 0.5s ease-out;
}

header .col1 { transition-delay: 1s; }
header .col2 ul li.item1 { transition-delay: 1.2s; }
header .col2 ul li.item2 { transition-delay: 1.3s; }
header .col2 ul li.item3 { transition-delay: 1.4s; }
header .col2 ul li.item4,
header .col3 .phone-wrap, 
header .language-wrap, 
header .col4 .burger-wrap,
.mp_s1 h1,
.mp_s1 .bg { transition-delay: 1.5s; }
.mp_s1 h3 { transition-delay: 1.6s; }

.animated {
    header .col1, 
    header ul li, 
    header .col3 .phone-wrap, 
    header .language-wrap, 
    header .col4 .burger-wrap,
    .mp_s1 h1, 
    .mp_s1 h3 {
        transform: translateX(0px);
        opacity: 1;
    }
    .mp_s1 .bg {
        opacity: 1;
    }
}

.myAnimate{
    opacity: 0;
    transform: translateY(25px);
    transition: all 0.6s cubic-bezier(0.08, 0.45, 0.44, 0.97);
}
.myAnimate.active{
    opacity: 1;
    transform: translateY(0px);
}

